<template>
  <v-container>

    <!--    从 "教师面板" 到 "计算一本率" 那个框框-->
    <v-row>
      <v-col>
        <v-card>
          <v-card-title>教师面板：学生信息管理</v-card-title>
          <v-card-subtitle style="margin-top: 1vh;">欢迎，教师{{ supervisor }}!</v-card-subtitle>

          <v-btn class="blue white--text" tile style="margin-top: 1vh; margin-bottom: 1vh; width: 100%">
            您所管理的班级为：{{manageClass.toString()}}
          </v-btn>

          <!--          新建班级以及一本率-->
          <v-col>
            <v-form>
              <v-container class="align-center">
                <v-row>
                  <v-col cols="24" sm="4" md="3">
                    <v-text-field
                        label="请输入新建班级号："
                        style="margin-left: 20%"
                        v-model="classNewNum"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="24" sm="3" md="2">
                    <v-btn color="success" style="margin-top: 2vh" @click="addNewClass">创建班级</v-btn>
                  </v-col>

                  <v-col cols="24" sm="6" md="2">
                    <v-text-field
                        label="请输入查询班级号："
                        v-model="classNumForRate"
                    ></v-text-field>
                  </v-col>

                  <v-col cols="24" sm="6" md="2">
                    <v-text-field
                        label="请输入一本线："
                        v-model="testRank"
                    ></v-text-field>
                  </v-col>

                  <v-col cols="24" sm="4" md="2">
                    <v-btn color="blue" class="white--text" style="margin-top: 2vh" @click="calRate">计算一本率</v-btn>
                  </v-col>
                </v-row>
              </v-container>
            </v-form>
          </v-col>
        </v-card>
      </v-col>
    </v-row>


    <!--    表格部分数据-->
    <v-row>
      <v-col>
        <v-data-table
            :headers="header1"
            :items="stdData"
            sort-by="class"
            class="elevation-1"
        >
          <template v-slot:top>
            <v-toolbar flat>
              <!--              chooseTitle: 学生信息管.理-->
              <v-toolbar-title>{{ choosedTitle }}</v-toolbar-title>
              <v-divider class="mx-4" inset vertical></v-divider>
              <v-spacer></v-spacer>
              <v-dialog  v-model="dialog" max-width="500px">
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                      color="success"
                      dark
                      class="mb-2"
                      style="margin-left: 1vh;margin-right: 1vh"
                      @click="getStudent"
                  >刷新
                  </v-btn>
                  <v-btn
                      color="indigo"
                      dark
                      class="mb-2"
                      v-bind="attrs"
                      v-on="on"
                  >
                    新建
                  </v-btn>

                </template>
                <v-card>
                  <v-card-title>
                    <!--                    formTitle : 新建/编辑-->
                    <span class="text-h5">{{ formTitle }}</span>
                  </v-card-title>

                  <!--                  新增数据的验证逻辑-->
                  <!--                  新建 弹出card的卡片-->
                  <v-card-text>
                    <v-container>
                      <v-row>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field v-model="editedItem.studentID" label="学号"></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field
                              v-model="editedItem.name"
                              label="姓名"
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field
                              v-model="editedItem.classID"
                              label="班号"
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field
                              v-model="editedItem.score"
                              label="分数"
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field
                              cols="12"
                              sm="6"
                              md="4"
                              v-model="editedItem.passwd"
                              label="密码"
                          ></v-text-field>
                        </v-col>
                      </v-row>
                    </v-container>
                  </v-card-text>

                  <!--                  弹出的 新建按钮 card的取消和保存-->
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn
                        color="blue darken-1"
                        text
                        @click="close"
                    >
                      取消
                    </v-btn>
                    <v-btn
                        color="blue darken-1"
                        text
                        @click="save"
                    >
                      保存
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>

              <!--              按下小垃圾桶按钮触发的框框-->
              <v-dialog v-model="dialogDelete" max-width="20%">
                <v-card>
                  <v-card-title class="text-h3 red--text text-center" style="margin-bottom: 1.5vh">是否删除该学生数据？
                  </v-card-title>
                  <v-divider></v-divider>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="red darken-1" text @click="deleteItemConfirm">确定</v-btn>
                    <v-btn color="blue darken-1" text @click="closeDelete">取消</v-btn>
                    <v-spacer></v-spacer>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-toolbar>
          </template>


          <!--            小铅笔, 小垃圾桶图标, 刷新触发的事件-->
          <template v-slot:item.actions="{ item }">
            <v-icon
                small
                class="mr-2"
                @click="editItem(item)"
            >
              mdi-pencil
            </v-icon>
            <v-icon
                small
                @click="deleteItem(item)"
            >
              mdi-delete
            </v-icon>
          </template>
          <template v-slot:no-data>
            <v-btn
                color="primary"
                @click="initialize"
            >
              Reset
            </v-btn>
          </template>

        </v-data-table>
      </v-col>
    </v-row>

    <!--    我要在这里加一个表格 我是杨沛霖 -->
    <v-row>
      <v-col>
        <!--        :headers是表格的表头-->
        <!--        :items是要显示的数据-->
        <v-data-table
            :headers="header2"
            :items="schoolDetails"
            sort-by="class"
            class="elevation-1"
        >
          <template v-slot:top>
            <v-toolbar flat>
              <!--              chooseTitle: 学生信息管.理-->
              <v-toolbar-title>大学报考志愿情况查看</v-toolbar-title>

              <v-divider class="mx-4" inset vertical></v-divider>
              <v-spacer></v-spacer>
              <!--              这里可以放上大学信息的下拉框-->
              <!--              这里是大学信息的下拉框, 用来选择大学-->
              <v-col cols="12" md="4">
                <v-combobox
                    v-model="collegeChoosen"
                    :items="getCollegeName(entireCollegeInformation)"
                    label="下拉框选择大学"
                    required
                    chips
                ></v-combobox>
              </v-col>

              <v-spacer></v-spacer>
              <!--              这个地方放上按钮-->
              <v-btn
                  color="indigo"
                  dark
                  class="mb-2"
                  style="margin-left: 1vh;margin-right: 1vh"
                  @click="getStudentSituation"
              >获取报考该大学的信息
              </v-btn>
              <v-btn
                  color="success"
                  dark
                  class="mb-2"
                  style="margin-left: 1vh;margin-right: 1vh"
                  @click="getAllCollegeInformation"
              >刷新获取所有大学列表
              </v-btn>
            </v-toolbar>
          </template>

        </v-data-table>

      </v-col>
    </v-row>

  </v-container>
</template>



<script>
import axios from "axios";

export default {
  name: 'Contacts',
  data: () => ({

    snack: true,
    //界面数据
    supervisor: '',
    stdGet: '',
    stdUpdate: '',
    stdDelete: '',
    stdRegister: '',

    //用于创建班级以及查询一本率
    classNewNum:'',
    classNumForRate:'',
    testRank:'',
    manageClass:[],
    selectPage: '',
    choosedTitle: '学生信息管理',

    //账号<20 8<=密码<20
    dialog: false,
    dialogDelete: false,
    header1: [
      {
        text: '学号',
        align: 'start',
        sortable: false,
        value: 'studentID',
      },
      {text: '姓名', value: 'name'},
      {text: '班号', value: 'classID'},
      {text: '分数', value: 'score'},
      {text: '排名', value: 'myRank'},
      {text: '修改', value: 'actions', sortable: false},
    ],
    stdData: [],
    editedIndex: -1,
    editedItem: {
      studentID: '',
      name: '',
      classID: '',
      score: '',
      passwd: '',
    },
    defaultItem: {
      studentID: '',
      passwd: '',
      name: '',
      score: '',
      classID: '',
    },
    test_url: "",//测试的url
    test_var1: "",//测试的var1
    test_var2: "",//测试的var2

    // 我 杨沛霖 要在这里加数据
    header2:[
      {text: '班级', value: 'classid'},
      {text: '学生学号', value: 'studentid'},
      {text: '学生名字', value: 'name'},
    ],

    // 最终选择的, 要查看的大学报考情况的大学
    collegeChoosen:[],

    schoolDetails: [],

    // 所有信息
    entireCollegeInformation:[],

  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? '新建' : '编辑'
    },
  },

  watch: {
    dialog(val) {
      val || this.close()
    },
    dialogDelete(val) {
      val || this.closeDelete()
    },
  },

  created() {
    this.initialize()
  },

  mounted: function(){
    this.getAllCollegeInformation();
  },

  methods: {

    // 杨沛霖 用的函数

    // 看看有哪些班的哪些人报了这个大学
    getStudentSituation:function(){
      var url = "http://localhost:9090/balance/getDetail";
      var that = this;
      //alert("进入查询该大学的报考信息页面");

      if(this.collegeChoosen[0] === undefined){
        alert("请选择大学");
      }else{
        axios({
          url : url,
          method : 'post',
          params:{
            id: (this.entireCollegeInformation.find((item)=> item.name === this.collegeChoosen)).collegeID,
          }
        })
            .then(function(response) {

                if (response.data.code === "0") { // 成功
                  alert("查询该大学的报考信息成功");
                  //alert("学校的id为: "+ JSON.stringify(that.id));
                  that.schoolDetails = response.data.data;
                } else if (response.data.code === "1") { // 失败
                  alert("查询该大学的报考信息失败");
                  // alert("学校的id为: " + JSON.stringify(that.id));
                } else { //不可能的情况
                  alert("查询该大学的报考信息 不可能的情况")
                }

            }).catch(function(){
          alert("查询该大学的报考信息 报错!!!!!!!!!")
        })
      }



    },

    // 向后端请求, 获得所有大学的信息
    getAllCollegeInformation:function() {
      var that = this;
      var url = 'http://localhost:9090/college/all';
      axios.defaults.headers.common['token'] = localStorage.getItem("token");

      axios({
        url:url,
        method:'get',
        params:null,
      })
          .then(function(response){
            //alert("已刷新所有大学的信息");
            that.entireCollegeInformation=response.data.data;
          })
          .catch(function(){
            alert("刷新所有大学的信息失败");
          })
    },

    // 下拉框要用的, 大学的名字
    getCollegeName(ObjList){
      var list = new Array();
      for(var i = 0;i<ObjList.length;i++){
        list.push(ObjList[i]['name'])
      }
      return list;
    },

    // 高杨用的函数

    addNewClass:function (){
      var that = this;
      axios({
        url: "http://127.0.0.1:9090/teacher/create_class",
        method: 'post',
        params:{
          id: that.classNewNum,
        }
      })
          .then(function (response) {
            alert("班级创建状态：" + response.data.msg);
            that.manageClass.push(that.classNewNum);
          })
    },

    calRate:function (){
      var that = this;
      axios({
        url: "http://127.0.0.1:9090/teacher/get_1_Rate",
        method: 'post',
        params:{
          id: that.classNumForRate,
          rank1Score: that.testRank
        }
      })
          .then(function (response) {
            alert("您所查询的班级一本率为：" + response.data.data);
          })
    },

    // 学生信息管理
    //获取信息
    getStudent() {
      var that = this;
      var url = "http://127.0.0.1:9090/student/all";

      axios({
        url: url,
        method: 'get',
      })
          .then(function (response) {
            Object.assign(that.stdData, response.data.data);
            that.stdData.push();
          })
    },

    //更新信息
    updateStudent() {
      var that = this;
      var url = "http://127.0.0.1:9090/student/update";

      axios({
        url: url,
        method: 'post',
        params: {
          id: that.editedItem.studentID,
          name: that.editedItem.name,
          passwd: that.editedItem.passwd,
          classID: that.editedItem.classID,
          score: that.editedItem.score,
          rank: that.editedItem.myRank,
        }
      })
          .then(function (response) {
            alert("更新信息状态" + response.data.msg);
          })

    },

    // 删除学生信息
    deleteStudent(editedindex) {
      var that = this;
      var url = "http://127.0.0.1:9090/student/delete";

      axios({
        url: url,
        method: 'post',
        params: {
          id: that.stdData[editedindex]['studentID'],
        }
      })
          .then(function (response) {
            alert("删除操作状态：" + response.data.msg);
          })
    },

    //新增学生信息
    registerStudent() {
      var that = this;
      var url = "http://127.0.0.1:9090/student/insert";

      axios({
        url: url,
        method: 'post',
        params: {
          id: that.editedItem.studentID,
          name: that.editedItem.name,
          passwd: that.editedItem.passwd,
          classID: that.editedItem.classID,
          score: that.editedItem.score,
        }
      })
          .then(function (response) {
            alert("注册操作状态：" + response.data.msg);
          })
    },


    mounted(){
      this.getStudent();
    },


    initialize() {
      this.stdData = [
        ]
    },

    editItem(item) {
      this.editedIndex = this.stdData.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },

    deleteItem(item) {
      this.editedIndex = this.stdData.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialogDelete = true
    },

    deleteItemConfirm() {
      this.deleteStudent(this.editedIndex);
      this.stdData.splice(this.editedIndex, 1)
      this.closeDelete()
    },

    close() {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    closeDelete() {
      this.dialogDelete = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    save() {
      if (this.editedIndex > -1) {
        Object.assign(this.stdData[this.editedIndex], this.editedItem);
      } else {
        this.stdData.push(this.editedItem)
      }
      if (this.editedIndex === -1) {
        this.registerStudent(this.editedIndex);
      } else {
        this.updateStudent(this.editedIndex);
      }
      this.close()
    },
  },
}
</script>
